.inputContainer {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-bottom: 40px;

	height: 100px;
  }
  
  .inputSearch {
	position: relative;
	width: 200px;
	height: 40px;
	margin: 0 10px;
  }
  
  .inputSearch input {
	width: 100%;
	height: 100%;
	border: 2px solid #ccc;
	border-radius: 20px;
	padding: 5px 20px;
	font-size: 16px;
	outline: none;
	transition: all 0.3s ease;
  }
  
  .inputSearch input:hover,
  .inputSearch input:focus {
	border-color: #0077ff;
	box-shadow: 0 0 5px rgba(0, 119, 255, 0.5);
  }
  
